/* ///////////----------------------------------- */
@import url("https://fonts.googleapis.com/css?family=Raleway");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

:root {
  --gold: #f4a322;
}

body {
  margin: 0;
  padding: 0;
  font-family: Raleway, sans-serif;
  background-color: gainsboro;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}


/* Style root Div */
  #root > div {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  max-width: 960px;
  min-width: 576px;
  background-color: white;
  min-height: 100vh;
  margin: 0 auto;
  -webkit-box-shadow: 0 0 1em rgba(0,0,0,0.3);
          box-shadow: 0 0 1em rgba(0,0,0,0.3);
}

header, main, footer {
  padding: 1.5rem;
}

header {
  display: -ms-flexbox;
  display: flex;
  background-color: var(--gold);
}

header h1 {
  margin: 0;
  color: white;
  text-shadow: 0.1em 0.1em 0.25em rgba(0,0,0,0.25);
}

header .material-icons.larger {
  font-size: 1.25em;
  vertical-align: text-bottom;
}

header nav {
   margin-left: auto;
   font-size: 20px;

}

main {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

footer {
  margin-top: auto;
  text-align: center;
}

form {
  text-align: center;
  margin-bottom: 1em;
}

form input, form select {
  display: inline-block;
  font-size: 1.25em;
  border: 1px solid lightgrey;
  margin: 5px;
}





*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.container {
  width: 80%;
  margin: 80px auto;
}

form {
  max-width: 50%;
  padding: 10px;
  border-left: 1px solid #dedede;
  border-right: 1px solid #dedede;
  margin: 75px auto;
}

input {
  font-family: Raleway, sans-serif;
  display: block;
  width: 80%;
  margin: 0 auto;
  padding: 10px;
}

input[type="text"],
input[type="password"]{
  margin-bottom: 20px;
  border: none;
  border-bottom: 1px solid #efefef;
  position: relative;
  -webkit-transition: border .55s;
  -o-transition: border .55s;
  transition: border .55s;
}

input[type="text"]:focus,
input[type="password"]:focus{
  outline: none;
  border: none;
  border-bottom: 1px solid #f8c573;
}

input[type="submit"]{
  width: 60%;
  background: #f5b042;
  border: none;
  border-radius: 50px;
  text-transform: uppercase;
  -webkit-transition: background-color .5s;
  -o-transition: background-color .5s;
  transition: background-color .5s;
}

input[type="submit"]:hover{
  background: #f4a322;
  cursor: pointer;
}
.logino{
  font-size: 25px;
}


.map-marker-link {
  display: block;
  color: white;
  background-color: rgba(0,0,0,0.5);
  border-radius: 0.25em;
  font-size: 1.25em;
  padding: .4em .75em;
  text-decoration: none;
  text-shadow: 0.25em 0.25em 0.25em rgba(0,0,0,.5);
}
 .map-marker-link .material-icons {
  font-size: 1.25em;
  display: inline;
  vertical-align: bottom;
}


.search-result {
  margin: .5em auto;
  padding: 1em;
  border-radius: .5em;
  background-color:  var(--gold);
  display: grid;
  grid-template-areas: "content button"
}

.search-result > div {
  grid-area: content;
}

.btn {
  display: inline-block;
  border-radius: 2em;
  padding: .75em 1.5em;
  -webkit-box-shadow: 0 0 .25em rgba(0,0,0,0.25);
          box-shadow: 0 0 .25em rgba(0,0,0,0.25);
  text-decoration: none;
  -webkit-transition: background-color .25s, -webkit-box-shadow .25s;
  transition: background-color .25s, -webkit-box-shadow .25s;
  -o-transition: background-color .25s, box-shadow .25s;
  transition: background-color .25s, box-shadow .25s;
  transition: background-color .25s, box-shadow .25s, -webkit-box-shadow .25s;
}

.btn:hover {
  -webkit-box-shadow: 0 0 .4em rgba(0,0,0,0.5);
          box-shadow: 0 0 .4em rgba(0,0,0,0.5);
}

.btn-book {
  background-color: lightgrey;
  color: black;
  margin-left: auto;
  grid-area: button;
}

.btn-book:hover {
  background-color: white;
}

.btn-splash {
  font-size: .5em;
  margin-top: 1em;
  text-align: center;
  color: white;
  font-weight: bold;
  background-color: #0b5dee/* #f4a322; */
}


.splash {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: center;
      align-items: center;
  background-color: var(--gold);
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: 25%;
}

.splash > .hero {
  display: -ms-flexbox;
  display: flex;
}

.splash > .hero > img {
  mix-blend-mode: hard-light;
}

.splash > .stuff {
  font-family: Raleway, sans-serif;
  font-size: 3em;
  color: white;
  padding: 1em 1em 0em 1em;
  text-align: bottom;
}


